  display: flex;
  flex-direction: column;
  flex: 1;
  text-align: left;
}
.expanded-table-row {
  background: #333;
  padding-top: 10px;
  padding-bottom: 10px;
}
.table-expanded-item p {
  margin-bottom: 0px;
}
</style>
